import React, {useState} from 'react';
import { Figure, Image, ButtonGroup, Button, Form } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUser } from '@fortawesome/free-solid-svg-icons';
import { Link } from 'react-router-dom';
import {Elements, StripeProvider, CardElement, injectStripe} from 'react-stripe-elements';
import './Profile.css'

export const Profile = () => {
  const [userProfile, setUP] = useState(null);

  return (
    <>
      <div className="profile-content">
        <ProfilePic profile={userProfile}/>
        <UserInfo profile={userProfile} />
      </div>
      <PaymentInfo />
    </>
  )
}

const ProfilePic = (props) => {
  return (
    <div className="profile-pic">
        <Figure className="user-pic">
          {props.profile ? 
            <Image src={props.profile.userImageSrc} thumbnail /> :
            <FontAwesomeIcon icon={faUser} style={{maxHeight: "200px", width: "150px", minHeight: "180px"}}/>}
            <Link to="/Profile/EditProPic"><Figure.Caption>Edit Picture</Figure.Caption></Link>
        </Figure>
    </div>
  );
}

const UserInfo = () => {
  return (
    <div className="user-info">
      Member since: <br/>
      Current expiration: <br/>
    </div>
  )
}

const PaymentInfo = () => {
  const [updatePayment, setUpPayment] = useState(false);
  // const stripePublishableKey = "enter publishable key here";
  // return (
  //   <StripeProvider apiKey={stripePublishableKey}>
  //     <Elements>
  //       <div className="checkout">
  //         <p>Would you like to complete the purchase?</p>
  //         <CardElement />
  //         <button onClick={()=>console.log('here')}>Send</button>
  //       </div>
  //     </Elements>
  //   </StripeProvider>
  // )
  return (
    <div className = "payment-info">
      {!updatePayment ? 
        <h3 onClick={()=>setUpPayment(true)}>Click here to update payment</h3> : 
        <PaymentForm setUpPayment={setUpPayment}/>}
    </div>
  )
}

const PaymentForm = (props) => {
  const [paymentInfo, setPaymentInfo] = useState('Visa ending in 6969');

  const handleSubmit = (event) => {
    event.preventDefault();
    alert(`${paymentInfo} Accepted`);
    props.setUpPayment(false);
  }

  return (
    <Form className="user-payment" onSubmit={handleSubmit}>
      <Form.Label>
        Choose Card (Provided through Stripe)
      </Form.Label>
      <Form.Control as="select" onChange={(event)=>setPaymentInfo(event.target.value)}>
        <option>Visa ending in 6969</option>
        <option>Amex ending in 0420</option>
      </Form.Control>
      <ButtonGroup>
        <Button type="submit">Submit Changes</Button>
        <Button onClick={()=>props.setUpPayment(false)} variant={"warning"}>Cancel Changes</Button>
      </ButtonGroup>
    </Form>
  )
}